body {
    margin: 0;
    padding: 0;

    
}

.Sum {
    text-align: center;
    display: inline-block;
    width: 420px;
    background: #CFD8DC;
}

.Sum h3 {
    margin: 0;
    padding:  5px 0 5px 0 ;
    font-size: 1.2em;
    background: #212121;
    color: #E0E0E0;
}

.Sum p {
    color: #607D8B;
    font-size: 1.1em;
    min-height: 500px;
}

.Sum button {
    border: none;
    margin: 10px;
    margin-bottom: 30px;
    padding: 5px;
    width: 120px;
    border-radius: 5px;
}

.Sum a {
    font-family: 'Pridi', serif;   
    text-decoration: none;
    color: whitesmoke;
}

.Back {
    background: #2196F3;
    transition: all 0.8s;
}

.Fin {
    background: #4CAF50;
    transition: all 0.8s;
}

.Back:hover {
    background: #64B5F6;
}

.Fin:hover {
    background: #8BC34A;
}